<template>
  <div id="menuarea">
    <div>
      <b-button pill variant="outline-primary" @click="setSubMenu(0)">即時監控</b-button>
      <b-button pill variant="outline-primary" @click="setSubMenu(1)">淹水感測器</b-button>
      <b-button pill variant="outline-primary" @click="setSubMenu(2)">水位</b-button>
      <b-button pill variant="outline-primary" @click="setSubMenu(3)">雨量</b-button>
      <b-button pill variant="outline-primary" @click="setSubMenu(4)">天氣概況</b-button>
      <b-button pill variant="outline-primary" @click="setSubMenu(5)">系統管理</b-button>
      <b-button pill variant="outline-primary" @click="setSubMenu(6)">相關連結</b-button>
      <b-button pill variant="outline-primary" @click="setSubMenu(7)">歷史資料</b-button>
    </div>
    <hr />
    <b-card no-body :header="subMenuTitle">
      <b-list-group flush class="menuItem">
        <menu-list
          v-for="(child, index) in subMenuContext"
          :key="index"
          :item="child"
          @showPage="onShowPage"
        ></menu-list>
      </b-list-group>
    </b-card>

    <b-modal id="pageModal" size="xl" :title="pageTitle" scrollable centered :hide-footer="true">
      <router-view />
    </b-modal>
  </div>
</template>
<style scoped>
#nav {
  padding: 30px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
}

#menuarea {
  padding: 20px;
}

.menuItem {
  text-align: left;
}
</style>

<script>
import MenuList from "@/views/MenuList";
// @ is an alias to /src
export default {
  name: "myMenu",
  components: {
    MenuList
  },
  data() {
    return {
      pageTitle: "",
      treeData: {
        name: "Root",
        children: [
          {
            name: "即時監控",
            children: [
              { name: "即時影像" },
              { name: "CCTV斷線警告" },
              { name: "移動式抽水機監控系統" }
            ]
          },
          {
            name: "淹水感測器",
            children: [
              { name: "淹水警戒" },
              { name: "淹水感測器塗層" },
              { name: "演水範圍預報" }
            ]
          },
          {
            name: "水位",
            children: [
              {
                name: "樹狀大排/測站查詢功能",
                children: [{ name: "即時水位" }]
              },
              {
                name: "水位警戒",
                children: [{ name: "警戒狀態" }, { name: "水位警報通知紀錄" }]
              },
              { name: "即時報表" }
            ]
          },
          {
            name: "雨量",
            children: [
              { name: "即時雨量" },
              { name: "雨量預報" },
              { name: "雨量警戒" },
              { name: "即時報表" }
            ]
          },
          { name: "天氣概況", children: [{ name: "雷達回波圖" }] },
          {
            name: "系統管理",
            children: [
              { name: "帳號管理" },
              { name: "雨量警戒管理" },
              { name: "水位警戒管理" },
              { name: "淹水警戒管理" },
              { name: "抽水站管理" },
              { name: "水閘門管理" },
              { name: "滯洪池管理" },
              { name: "抽水機管理" },
              { name: "淹水感測器管理" },
              { name: "防汛備料管理" },
              { name: "歷史圖資管理" },
              { name: "歷史報表管理" },
              { name: "相關連結管理" }
