              { name: "歷史決策建議分析報告" }
            ]
          }
        ]
      },
      activeIndex: 0
    };
  },
  computed: {
    subMenu() {
      return this.treeData.children[this.activeIndex];
    },
    subMenuTitle() {
      return this.treeData.children[this.activeIndex].name;
    },
    subMenuContext() {
      return this.treeData.children[this.activeIndex].children;
    }
