<template>
  <b-list-group-item :button="true">
    <div v-if="isFolder" @click.stop="toggle">
      <span>{{ isOpen ? '-' : '+' }}</span>
      {{ item.name }}
    </div>
    <div v-else @click.stop="goLink">{{ item.name }}</div>

    <b-list-group v-show="isOpen" v-if="isFolder">
      <menu-list class="item" v-for="(child, index) in item.children" :key="index" :item="child" @showPage="passEvt"></menu-list>
    </b-list-group>
  </b-list-group-item>
</template>
<style scoped>
ul {
  text-align: left;
}

.item {
  cursor: pointer;
}
</style>
<script>
export default {
  name: "MenuList",
  props: {
    item: Object,
    topLevel: Boolean
  },
  data() {
    return {
      isOpen: false
    };
  },
  computed: {
    isFolder: function() {
      return this.item.children && this.item.children.length;
    }
  },
  methods: {
    toggle: function() {
      if (this.isFolder) {
        this.isOpen = !this.isOpen;
      }
    },
    goLink() {
      this.$emit("showPage", { name: this.item.name });
    },
    passEvt(arg){
      this.$emit("showPage", arg);
    }
  }
};
</script>